.LbSquare {
    text-align: center;
    margin: auto;
    display: table;
}

.LbSquare-label{
    display: table-cell;
    vertical-align: middle;
    display: inline-block;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-size: 90%
}

.LbSquare-input {
    width: 0;
    height: 0;
    border-style: none;
}

input[type="radio"]:checked+.LbSquare-label{
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.5), 0 6px 20px 0 rgba(0, 0, 0, 0.45);
}

input[type="radio"]:checked+.LbSquare-label+svg{
    display: block;
}

input[type="radio"]+.LbSquare-label+svg{
    display: none;
}
 