.BookBe-M {
    position: absolute;
    top:350px;
    right:0;
    height: 900px;
}

.BookBe-S{
  text-align: center;
  padding: auto;
  margin: auto;
}

.bn {
  cursor: pointer;
  text-align: center;
  padding: auto;
  margin: auto;
}

.BookBe-M-div {
  position: -webkit-sticky;
  position: sticky;
  top: 100px;
  cursor: pointer;
  text-align: center;
  padding: auto;
  margin: auto;
  height: 400px;
  background: #0b996c; /* Old browsers */
  background: -moz-linear-gradient(top, #0b996c 0%, #0b996c 100%); /* FF3.6-15 */
  background: -webkit-linear-gradient(top, #0b996c 0%,#0b996c 100%); /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to bottom, #0b996c 0%,#0b996c 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#0b996c', endColorstr='#0b996c',GradientType=0 ); /* IE6-9 */
  border: 2px solid #37d67f;

  
}

.BookBe-M-cell{
  text-align: left;
}

.BookBe-M-button{
  max-width: 270px;
}

.red {
    color: red;
}