*{
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px) {

  html {
      font-size: 16px !important;
  }

  .es-hidden {
      display:none;
  }

  .es-inline {
      display: inline-block;
      float:left;
  }

  .es-t1 {
      font-size: 2rem;
      font-family: Arial, Helvetica, sans-serif;
      font-weight: 500;
      line-height: 1;
  }
  
  .es-t2 {
      font-size: 1.5rem;
      font-family: Arial, Helvetica, sans-serif;
      font-weight: 500;
      line-height: 1;
  }
  
  .es-t3 {
      font-size: 1rem;
      font-family: Arial, Helvetica, sans-serif;
      font-weight: 600;
      line-height: 1;
  }
  
  .es-t4 {
      font-size: 0.8rem;
      font-family: Arial, Helvetica, sans-serif;
      font-weight: 700;
      line-height: 1;
  }
  
  .es-t5 {
      font-size: 1.1rem;
      font-family: "Times New Roman", Times, serif;
      font-weight: bold;
  }

  .es-tl-roundcorner {
      border-top-left-radius: 5px;
  }

  .es-tr-roundcorner {
      border-top-right-radius: 5px;
  }

  .es-br-roundcorner {
      border-bottom-right-radius: 5px;
  }

  .es-bl-roundcorner {
      border-bottom-left-radius: 5px;
  }

  .min-col-es-12 {
      min-width: 100%;
  }

  .col-es-1 {
      width: 8.33%;
  }

  .col-es-2 {
      width: 16.66%;
  }

  .col-es-3 {
      width: 25%;
  }

  .col-es-4 {
      width: 33.33%;
  }

  .col-es-5 {
      width: 41.66%;
  }

  .col-es-6 {
      width: 50%;
  }

  .col-es-8 {
      width: 66.66%;
  }

  .col-es-10 {
      width: 83.33%;
  }

  .col-es-12 {
      width: 100%;
  }

  .row-es-0 {
      height: 0%;
  }

  .row-es-1 {
      height: 8.33%;
  }

  .row-es-2 {
      height: 16.66%;
  }

  .row-es-3 {
      height: 25%;
  }

  .row-es-4 {
      height: 33.33%;
  }

  .row-es-6 {
      height: 50%;
  }

  .row-es-8 {
      height: 66.66%;
  }

  .row-es-9 {
      height: 75%;
  }

  .row-es-10 {
      height: 83.33%;
  }

  .row-es-11 {
      height: 91.66%;
  }

  .row-es-12 {
      height: 100%;
  }

  .sq-es-1 {
      width: 8.33%;
      padding-top: 8.33%;
  }

  .sq-es-6 {
      width: 50%;
      padding-top: 50%;
  }
}

