.BookBa-content{
    background: #0b996c; /* Old browsers */
    background: -moz-linear-gradient(top, #0b996c 0%, #0b996c 100%); /* FF3.6-15 */
    background: -webkit-linear-gradient(top, #0b996c 0%,#0b996c 100%); /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(to bottom, #0b996c 0%,#0b996c 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#0b996c', endColorstr='#0b996c',GradientType=0 ); /* IE6-9 */


}

.BookBa-input{
    border: 3px solid #cfe638; /* Old browsers */
}

.bordert-none {
    border-top-style: none;
  }
  
  .borderb-none {
    border-bottom-style: none;
  }

  .borderr-none {
    border-right-style: none;
}

  .tl-roundcorner {
    border-top-left-radius: 5px;
}

  .br-roundcorner {
    border-bottom-right-radius: 5px;
}

  @media only screen and (max-width: 600px) {
    .es-borderb-none{
        border-bottom-style: none;
    }
    .es-tr-roundcorner {
        border-top-right-radius: 5px;
    }
    .es-bl-roundcorner {
        border-bottom-left-radius: 5px;
    }
  }

  @media only screen and (min-width: 600px) {
    .s-borderb-none {
        border-bottom-style: none;
    }
    .s-tr-roundcorner {
        border-top-right-radius: 5px;
    }
    .s-bl-roundcorner {
        border-bottom-left-radius: 5px;
    }
  }

  @media only screen and (min-width: 768px) {
    .m-borderb {
        border-bottom-style: solid;
    }
    .m-borderr-none {
        border-right-style: none;
    }
  
    .m-tr-roundcorner {
        border-top-right-radius: 5px;
    }
    .m-tr-roundcorner-none {
        border-top-right-radius: 0px;
    } 
    .m-bl-roundcorner {
        border-bottom-left-radius: 5px;
    }
    .m-bl-roundcorner-none {
        border-bottom-left-radius: 0px;
    }

  }